$('document').ready(() => {

    if($(".js-modal").length > 0) {
        let positionScroll = 0,
            $window = $(window),
            player = null,
            $body = $('body');

        $('.js-open-modal').on('click', function() {
            let targetModal = $(this).data('target-modal');
            positionScroll = $window.scrollTop();
            $body.css('overflow', 'hidden');
            $('.page-wrapper').scrollTop(positionScroll);
            $(`.js-modal[data-target-modal=${targetModal}]`).fadeIn(500);

            if($(`#${targetModal}`).length > 0) {
                let videoId = $(`#${targetModal}`).data('video-id');
                player = new YT.Player(targetModal, {
                    videoId: videoId,
                    events: {
                        'onReady': onPlayerReady
                    },
					playerVars: {rel: 0}
                });

                function onPlayerReady(event) {
                    event.target.playVideo();
                }
            }
        });

        $('.js-close-modal').on('click', function() {
            $(this).closest('.js-modal').fadeOut(500);
            $body.css('overflow', '');
            $window.scrollTop(positionScroll);

            if(player) {
                player.destroy();
            }
        })
    }
});
