$('document').ready(() => {
    let $toggleMenu = $('.js-toggle-menu');

    if($toggleMenu.length > 0) {
        let $body = $('body'),
            nameClassForOpenMenu = 'menu-open',
            $mobileMenu = $('.js-mobile-menu'),
            $nav = $('.js-nav'),
            $window = $(window),
            windowWidth = $window.outerWidth(),
            positionScroll = 0,
            breakpoint = 576;

        const openMenu = () => {
            positionScroll = $window.scrollTop();
            $body.addClass(nameClassForOpenMenu);
            $('.page-wrapper').scrollTop(positionScroll);
        };

        const closeMenu = () => {
            $body.removeClass(nameClassForOpenMenu);
            $window.scrollTop(positionScroll);
        };

        $toggleMenu.on('click', (event) => {
            event.preventDefault();

            $body.hasClass(nameClassForOpenMenu)
                ? closeMenu()
                : openMenu();
        });

        const moveMenuWithResponsive = () => {
            let $placeMenuForMobile = $mobileMenu;
            let $placeMenuForDesktop = $('.js-nav-for-desktop');

            if(windowWidth < breakpoint) { // for mobile
                $placeMenuForMobile.prepend($nav);
            } else { // for desktop
                $placeMenuForDesktop.prepend($nav);
            };
        };
        moveMenuWithResponsive();

        $window.resize(() => {
            windowWidth = $window.outerWidth();
            moveMenuWithResponsive();

            if(windowWidth >= breakpoint && $body.hasClass(nameClassForOpenMenu)) {
                closeMenu();
            }
        });
    }
});