$('document').ready(() => {
    if($('.js-scroll-to').length > 0) {
        const $button = $('.js-scroll-to');

        $button.on('click', function(e) {
            e.preventDefault();

            const target = $(this).data('scroll-to');
            const offsetTop = $(`[data-scroll-target=${target}]`).offset().top;
            $('html, body').animate({
                scrollTop: offsetTop
            }, 1000);
        })
    }
});