$(document).ready(function () {
    if($('.js-get-price').length > 0) {
        let $button = $('.js-get-price'),
            $table = $('.js-price'),
            $head = $('.js-head-price-category'),
            headWidth = 'calc(40vw - 20px)';
            $window = $(window),
            isPopPlan = 0,
            offsetScroll = 0;

        const togglePopPlan = () => {
            if(isPopPlan) {
                $table.addClass('is-popular-plan');
            } else {
                $table.removeClass('is-popular-plan');
            }
            $('.cgt-tag--popular-plan').css('opacity', `${isPopPlan}`);
        };

        const initStyleForTable = () => {
            togglePopPlan();

            if($window.outerWidth() < 576) {
                $table.find('.pricing__table').css('width', `${60 + $button.length * 40}%`);
                $head.css('width', headWidth);
                $table.scrollLeft(offsetScroll);
            } else {
                $table.find('.pricing__table').css('width', '');
                $head.css('width', '');
            };
        };
        initStyleForTable();

        $window.resize(() => initStyleForTable());

        $button.on('click', function(e) {
            e.preventDefault();

            let $this = $(this);
            let indexEl = $button.index($this);
            let windowWidth = $window.outerWidth();
            let step = windowWidth * 0.4 - 20;
            offsetScroll = indexEl * step;
            headWidth = `calc(${40 * (indexEl + 1)}vw - ${20 * (indexEl + 1)}px)`;

            $button.removeClass('active');
            $this.addClass('active');
            $table.scrollLeft(offsetScroll);
            $head.css('width', headWidth);

            if($this.hasClass('js-button-popular-plan')) {
                isPopPlan = 1;
            } else {
                isPopPlan = 0;
            };

            togglePopPlan();
        });
    }
});