$('document').ready(() => {
    if($('.js-arsenal-tabs-content').length > 0) {

        let $content = $('.js-arsenal-tabs-content'),
            $placeMobile = $('.js-arsenal-tabs-place-mobile'),
            $placeDesktop = $('.js-arsenal-tabs-place-desktop'),
            $tabLink = $('.js-arsenal-tabs-link'),
            duration = 200,
            breakpointTablet = 768,
            $window = $(window);

        const moveContent = () => {
            let _windowWidth = $window.outerWidth();

            if(_windowWidth < breakpointTablet) { // mobile
                // e.append(s) — add 's' the last in 'e'.
                $content.each((index, item) => {
                    $placeMobile[index].append(item);
                });
                $content.css('display', '');

            } else { // desktop
                // e.append(s) — add 's' the last in 'e'.
                $placeDesktop.append($content);
            }
        };
        moveContent();

        $window.resize(() => {
            moveContent();
        });

        // Toggle tab content
        $tabLink.on('click', function() {
            let $this = $(this);
            let _windowWidth = $window.outerWidth();

            if(_windowWidth >= breakpointTablet && !$this.hasClass('active')) {
                let targetContent = $this.data('arsenal-tab');

                $tabLink.removeClass('active');
                $this.addClass('active');

                $content.closest('.active').stop().fadeOut(duration, function() {
                    $(this).removeClass('active');
                    $(`.js-arsenal-tabs-content[data-arsenal-content=${targetContent}]`).fadeIn(duration, function() {
                        $(this).addClass('active');
                    });
                });
            }
        });
    }
});