import Swiper from 'swiper';

$(document).ready(function () {

    if($('.js-photo-slider').length > 0) {
        let photoSlider = new Swiper ('.js-photo-slider', {
            loop: true,
            spaceBetween: 20,
            pagination: {
                el: '.swiper-pagination',
                type: 'bullets',
            },
            navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            }
        })
    }
});